import "./Navbar.css";
import "../styles.css";
import { NavLink, useLocation } from "react-router-dom";
import React, { useContext, useState } from "react";
import { PerformanceContext } from "../Contexts";

export const Navbar = ({ classOverride }) => {
  const { isLowPerformance, setIsLowPerformance } =
    useContext(PerformanceContext);
  const location = useLocation();
  return (
    <div className={`nav-container `}>
      <ul className={`navbar ${classOverride ?? "navbar-default"}`}>
        <li>
          <NavLink
            to="/#home"
            className="navlink"
            style={({ isActive }) =>
              isActive ? { backgroundColor: "#001414" } : {}
            }
          >
            Home
          </NavLink>
        </li>
        {/* <li>
          <NavLink
            to="/browse"
            className="navlink"
            style={({ isActive }) =>
              isActive ? { backgroundColor: "#002929" } : {}
            }
          >
            About
          </NavLink>
        </li>
        <li>
          <NavLink
            to="/create"
            className="navlink"
            style={({ isActive }) =>
              isActive ? { backgroundColor: "#002929" } : {}
            }
          >
            Create
          </NavLink>
        </li> */}
        <li>
          <NavLink
            to="/work-term-report"
            className="navlink"
            style={({ isActive }) =>
              isActive ? { backgroundColor: "#001414" } : {}
            }
          >
            Work Term Reports
          </NavLink>
        </li>
        <li onClick={() => setIsLowPerformance(!isLowPerformance)}>
          {isLowPerformance ? "Background on" : "Background off"}
        </li>
      </ul>
    </div>
  );
};
